.break-points{
  max-width: 540px;
  margin: 0 auto;
  @media screen and (min-width: 767px) {
      max-width: 690px;
  }
  @media screen and (min-width:991px){
      max-width: 930px;
  }
  @media screen and (min-width:1200px){
      max-width: 1110px;
  }
}
.p-section{
  padding: 2rem 1rem;

  @media screen and (min-width:575px) {
      padding: 3rem 1rem;
  }
  @media screen and (min-width:991px) {
      padding: 4rem 1rem;
  }
}